import React, { useState } from "react";

function Header(props) {
  return (
    <header style={{textAlign:'center'}}>
      <h4><a href='/' onClick={(e) => {
        e.preventDefault();
        props.onChangeMode();
      }}>{props.title}</a></h4>
    </header>
  )
}

function Nav(props) {
  return (
    <nav>
      <ol>
        {
          props.topics.map((a, i) => {
            return (
              <li key={i} > <a id={props.topics[i].id} href={`/read/${i}`}
                onClick={(e) => {
                  e.preventDefault()
                  props.onChangeMode(Number(e.target.id))
                }}
              >{props.topics[i].title} </a>
              </li>
            )
          })
        }
      </ol>
    </nav>
  )
}

function Article(props) {
  return (
    <article>
      <h5>{props.title}</h5>
      <p>{props.body}</p>
    </article>
  )
}

function Create(props) {
  return (
    <article >
      <h6></h6>
      <form onSubmit={ee => {
        ee.preventDefault()
        const title = ee.target.title.value;
        const body = ee.target.body.value;
        props.onCreate(title, body);
        console.log(title, '+', body)
      }}>
        <p><input name='title' placeholder='' /></p>
        <p><textarea name='body' placeholder='' /></p>
        <p><input type='submit' value='' onClick={(ev) => { }} /><span><input onClick={() => { props.setMode('read') }} type='button' value='' /></span></p>
      </form>

    </article>
  )
}

function Update(props) {
  const [title, setTitle] = useState(props.title)
  const [body, setBody] = useState(props.body)

  return (
    <article >
      <h6></h6>
      <form onSubmit={ee => {
        ee.preventDefault()
        const title = ee.target.title.value;
        const body = ee.target.body.value;
        props.onUpdate(title, body);
      }}>
        <p><input name='title' value={title} onChange={(e) => {
          setTitle(e.target.value)
        }} /></p>
        <p><textarea name='body' value={body} onChange={(e) => {
          setBody(e.target.value)
        }} />
        </p>
        <p><input type='submit' value='' /><span><input onClick={() => { props.setMode('read') }} type='button' value='' /></span></p>
      </form>
    </article>
  )
}

function Crud() {
  const [mode, setMode] = useState('welcome') // 
  const [id, setId] = useState(null) // (a id)  id 
  const [nextId, setNextId] = useState(9)

  const [topics, setTopics] = useState([
    { id: 1, title: ' ', body: '1' },
    { id: 2, title: '', body: '200ml' },
    { id: 3, title: '', body: '1/2ts' },
    { id: 4, title: '', body: '1' },
    { id: 5, title: ' ', body: '100g' },
    { id: 6, title: '', body: '1/4ts' },
    { id: 7, title: '', body: '1/4ts' },
    { id: 8, title: '', body: '' }
  ])
  let content = null;
  let subtn = null;

  if (mode == 'welcome') {
    content = <Article title=' ' body='~' />
  } else if (mode == 'read') {
    let title, body = null
    for (let i = 0; i < topics.length; i++) {
      if (topics[i].id == id) {
        title = topics[i].title;
        body = topics[i].body
      }
    }
    content = <Article title={title} body={body} />;

    subtn = <a style={{ margin: '0 15px' }} href={`/update/${id} `} onClick={(e) => {
      e.preventDefault();
      setMode('update');
    }}> </a>





  } else if (mode == 'create') {
    content = <Create setMode={setMode} onCreate={(_title, _body) => {
      const newTopic = { id: nextId, title: _title, body: _body }
      const newWorld = [...topics]
      newWorld.push(newTopic)
      setTopics([...newWorld])
      setMode('read')
      setId(nextId)
      setNextId(nextId + 1)
    }}
    />
  } else if (mode == 'update') {

    let title = null;
    let body = null;
    for (let i = 0; i < topics.length; i++) {
      if (topics[i].id == id) {
        title = topics[i].title;
        body = topics[i].body;
      }
    }

    content = <Update title={title} body={body} setMode={setMode}
      onUpdate={(title, body) => {
        console.log(title, body);
        const newWorld = [...topics]
        const updatedTopic = { id: id, title: title, body: body }
        for (let i = 0; i < newWorld.length; i++) {
          if (newWorld[i].id === id) {
            newWorld[i] = updatedTopic;
            break;
          }
        }
        setTopics(newWorld)
        setMode('read')
      }}
    />
  }

  return (
    <div className="crud_box">
      <Header title=" " onChangeMode={() => {
        setMode('welcome')

      }} />

      
      <Nav topics={topics} onChangeMode={(_id) => {
        setMode('read')
        setId(_id)
      }}
      />
      {content}
      <div style={{ display: 'flex', justifyContent:'center' }}>
        <a href="/create" onClick={(event) => {
          event.preventDefault();
          setMode('create')
        }}> </a><br />
        {subtn}
        {
          mode == 'read' ?

            <a href='/delete' onClick={(e) => {
              e.preventDefault();
              const box = [];
              for (let i = 0; i < topics.length; i++) {
                if (topics[i].id !== id) {
                  box.push(topics[i])
                }
              }
              setTopics(...[box])
              setMode('welcome')
            }}

            > </a>
            : ''

        }
      </div>
    </div>
  )
}

export default Crud