import { useRef, useState } from "react";

function Four() {
  const ex = useRef()
  let [idd, setId] = useState(''); // input
  let [txt, setTxt] = useState(['1','2','3']); // 


  let fuc1 = (event) => {
    setId(event.target.value)
  }

  return (
    <div className="tC">
      <div className="todo">
        <h4>To Do List<button className="allDel" onClick={(e)=>{
          let copy3 = [] 
          // copy3 = []
          setTxt(copy3)
          ex.current.focus();
        }}></button></h4>
        
        <div className="inputBox">
          <input autoFocus type="text" value={idd} placeholder=" "
            onChange={
              fuc1
            }
            ref={ex}
            />
          <input type="button" value="" onClick={
            () => {
              let copy2 = [...txt]
              copy2.push(idd)
              setTxt(copy2)
              setId('')
              ex.current.focus()
            }
          } />
        </div>
        <hr />
        <div >
          <ul>
            <TdList  />
          </ul>
        </div>
      </div>
    </div>
  )

  function TdList() {
    return (
      txt.map((aa, ii) => {
        return (
          <li key={ii}>
            <span>
              {txt[ii]}
            </span>
            <b onClick={() => {
              let copy = [...txt]
              copy.splice(ii, 1)
              setTxt(copy)
            }}>X</b>
          </li>
        )
      })
    )
  }
}

export default Four;