import { useState, useEffect } from "react";
import "./innovation.scss"
import { Link } from 'react-router-dom';
import { auth, db } from '../firebase-config';
import { signOut, onAuthStateChanged } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';


const ShopHam = ({ product }) => {
  const [look, setLook] = useState(false);

  // 
  const logout = async () => {
    await signOut(auth)
  }

  let backpage = useNavigate();
  let mov = () => { backpage('/login') }

  // 
  let [output, setOutput] = useState()

  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        // console.log('')
        // console.log(user)
        setOutput(user)
      } else {
        // console.log(" ")
        mov()
      }
    })
  }, [auth]);

  return (
    <>
      <div className="ham_box">
        <button onClick={() => {
          if (look === false) {
            setLook(true)
          } else {
            setLook(false)
          }
        }}>
          <img src={process.env.PUBLIC_URL + `/img/hamburger.png`} alt="" />
        </button>
        {look == true ? <div className="info">
          <ul>
            <li>
              <p style={{ wordBreak: "break-word" }}>{output.displayName}<span></span> </p>
              <p>.</p>
            </li>
            <li>
              <Link to='/shop'></Link>
            </li>
            <li>
              <Link to='/shopmade'></Link>
            </li>
            <li>
              <Link to='/shopmy'></Link>
            </li>
            <li>
              <Link onClick={logout}>
                
              </Link>
            </li>
          </ul>
        </div> : ''}
      </div>
      <div className="orange">

        <span>
          <Link to='/shop'> Orange Market</Link>
        </span>
      </div>
    </>
  )
}


export default ShopHam